@charset "UTF-8";
@font-face {
  font-family: "CerebriSansPro";
  src: url("../fonts/CerebriSansPro-Medium.ttf");
  font-weight: 500;
  font-style: normal;
}
@font-face {
  font-family: "CerebriSansPro";
  src: url("../fonts/CerebriSansPro-Bold.ttf");
  font-weight: 1000;
  font-style: bolder;
}
