img {
  max-width: 100%;
}

body {
  background-color: #ECEEF4;
  color: #484D54;
}

h1, h2, h3, h4, h5, h6 {
  font-family: "CerebriSansPro";
  font-weight: bolder;
  margin: 0;
  text-transform: capitalize;
}

h2 {
  font-size: 2rem;
}

h1 {
  font-size: 1.5rem;
}

a {
  text-decoration: none;
  color: #484D54;
}

p,
span,
div {
  font-family: "CerebriSansPro";
}

nav {
  position: fixed;
  bottom: 15px;
  left: 50%;
  transform: translateX(-50%);
  z-index: 999;
  backdrop-filter: blur(1rem);
  border-radius: 100px;
}
nav ul {
  border-radius: 100px;
  width: 430px;
  padding: 15px;
  display: flex;
  justify-content: space-between;
  background-color: rgba(255, 255, 255, 0.5);
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

.nav__item {
  transition: ease-out 0.3s;
  width: 70px;
  height: 70px;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  border-radius: 100%;
  background-color: white;
}

.nav__item:hover {
  transition: ease-out 0.3s;
  background-color: #5C9CFB;
}
.nav__item:hover img {
  filter: invert(100%) sepia(0%) saturate(0%) hue-rotate(306deg) brightness(106%) contrast(101%);
}

.wrapper {
  max-width: calc(100% - 2rem);
  margin: 2rem auto 0 auto;
}

/*
-----SETTINGS-----
Used with preprocessors and contain font, colors definitions, etc.
*/
/*
-----TOOLS-----
Globally used mixins and functions. 
It’s important not to output any CSS in the first 2 layers.
*/
/*
-----GENERIC-----
Reset and/or normalize styles, box-sizing definition, etc. 
This is the first layer which generates actual CSS.
*/
/*
-----ELEMENTS-----
Styling for bare HTML elements (like H1, A, etc.). 
These come with default styling from the browser so we can redefine them here.
*/
/*
-----OBJECTS-----
Class-based selectors which define undecorated design patterns,
for example the media object known from OOCSS
*/
/*
-----COMPONENTS-----
Specific UI components. This is where most of our work takes place.
We often compose UI components of Objects and Components
*/
/*
-----UTILITIES-----
Utilities and helper classes with ability to override anything which goes before
in the triangle, e.g. hide helper class
*/